.gpt3__possibility {
  display: flex;
}

.gpt3__possibility-image {
  flex: 1;
  display: flex;
  justify-content: start;
  align-items: center;
  margin-right: 2rem;
}

.gpt3__possibility-image img {
  width: 100%;
}

.gpt3__possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.gpt3__possibility-content h1{
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.gpt3__possibility-content p{
  font-size: 16px;
  line-height: 30px;
  color: var(--color-text);
  margin-bottom: 2rem;
}

.gpt3__possibility-content h4{
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71E5FF;
}

.gpt3__possibility-content h4:last-child{
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color:  #FF8A71;
}


@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;
  }

  .gpt3__possibility-image img {
    margin: 1rem 0;
  }

  .gpt3__possibility-content {
    margin-top: 2rem;
  }
}